body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App { 
  background: linear-gradient(to bottom right, rgb(65, 247, 238), rgb(56, 103, 255));
  background-attachment: fixed;
  background-repeat: no-repeat;
  height: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
}
.card .card-content{
  box-shadow: 0px 2px 45px 8px rgb(43, 109, 195);
  background: #2B2D41;
  color: #4E5279;
}

.fas{
  font-size: 50px;
  margin-top: 20px;
}

.fa-question-circle{
  font-size:20px;
  margin-top: 30px;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px)
}

.sex-orientation{
  display: -webkit-flex;
  display: flex; 
  
}
.sex{
  width: 100px;
  height: 100px;
  background: #2B2D41;
  color: #4E5279;
  margin:20px; 
  border-radius: 7px;
  text-align: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.female:hover{
  transition-duration: 0.5s;
  background-color: rgb(228, 36, 132);
  border: solid 2px #2B2D41;
  color:#2B2D41;
  background:#4E5279;
}

.male:hover{
  transition-duration: 0.5s;
  background-color: rgb(19, 64, 212);
  border: solid 2px #2B2D41;
  color:#2B2D41;
  background:#4E5279;
}
.weightCalc{
  color: rgba(128, 128, 128, 0.425);
  text-align: center;
}

.btn{
  background-color: #4E5279
}
.btn:hover:focus{
  background: linear-gradient(to bottom right, rgb(65, 77, 247), rgb(56, 103, 255));
}


Button{
  margin-left: 25%;
}
::-webkit-input-placeholder {
  color: grey;
  font-size: 20px;
  font-weight: 500;
}
::-ms-input-placeholder {
  color: grey;
  font-size: 20px;
  font-weight: 500;
}
::placeholder {
  color: grey;
  font-size: 20px;
  font-weight: 500;
}

input{
 color: white;
 font-weight: 700;
 font-size: 25px!important;

}

.input-inches, .input-feet{
  width: 120px!important;
}

.bmi-text{
  text-align: center;
}
.underweight{
  color: #3498DB;
}
.normal{
  color: rgb(97, 218, 97);
}
.obese{
  color: red;
}
.overweight{
  color: orange;
}

.title-of-app{
  -webkit-transform: translateY(-40px);
          transform: translateY(-40px);
  color:white;
    font-weight:bold;
    text-transform:uppercase;
    text-align:center;
    position:relative;
    font-family:"Poppins",sans-serif;
    text-shadow:0 1px 0 #239ad1f5,
                0 2px 0 #12c7e7f6,
                0 3px 0 #0dbfebf6,
                0 4px 0 #0cbcf1ef,
                0 5px 0 #10c2eef1,
                0 6px 0 #0f9aebda,
                0 7px 0 #0b9bdd,
                0 8px 0 #0ba5ec,
                11px 16px 20px #00000052;
  
}
